import http from "@/http-common";

class DataService {
    getAllMotorcycles() {
        return http.get(`/motorcycles/`);
    }
    getAllMotorcyclesByLicense(license) {
        return http.get(`/motorcycles/?license=` + license);
    }
    getAllMotorcyclesBySeatHeight(seatHeight) {
        return http.get(`/motorcycles/?seatHeight=` + seatHeight);
    }
    getAllMotorcyclesByBrand(brand) {
        return http.get(`/motorcycles/?brand=` + brand);
    }
    getAllMotorcyclesByType(type) {
        return http.get(`/motorcycles/?type=` + type);
    }
    getMotorcycle(id) {
        return http.get(`/motorcycles/` + id);
    }
    getBrands(){
        return http.get('/motorcycles/brands')
    }

    getTypes(){
        return http.get('/motorcycles/types')
    }

    sendMessage(message){
        return http.post('/contact', message)
    }
}

export default new DataService()