<template>
  <v-container>
    <app-bar />
    <v-row>
      <v-col class="mt-2" cols="12">
        <v-btn to="/compare" fab class="ma-2" outlined color="blue" small>
          <v-icon>
            mdi-arrow-left
          </v-icon>
        </v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col xs="12" lg="4">
        <v-card>
          <v-img :src=motor.image></v-img>
        </v-card>
      </v-col>
      <v-col>
        <v-row>
          <v-col xs="12" class="ml-8">
            <h2>
              <strong>{{ motor.brand }}</strong></h2>
            <p>{{ motor.motortype }}</p>
          </v-col>
          <v-col xs="12">
            <h2>{{ motor.cilinderSize }}</h2>
            <p>{{ motor.model }}</p>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import dataService from "@/services/DataService";
import AppBar from "@/components/app-bar-default.vue";

export default {
  name: "page-motorcyle",
  components: {AppBar},
  data() {
    return {motor: {}}
  },
  mounted() {
    dataService.getMotorcycle(this.$route.params.id).then((response) => {
      this.motor = response.data;
    })
  }
}
</script>

