<template>
  <v-container>
    <v-navigation-drawer  v-model="drawer"  color="white">
      <v-list-item elevation=2>
        <v-list-item-title class="title">
          <router-link to="/">
            <v-img to="/" max-height="56"
                   max-width="250" src="logo1.png"></v-img>
          </router-link>
        </v-list-item-title>
      </v-list-item>
      <v-list>
        <v-list-group value="Licenses">
          <template v-slot:activator="{ props }">
            <v-list-item
                v-bind="props"
                prepend-icon="mdi-passport"
                title="Rijbewijs"
            ></v-list-item>
          </template>
          <v-list-item height="50px" v-for="(title, i) in licenses" :key="i">
            <v-checkbox height="50px" v-model="additional_grouped" :value="title" @change="uniqueCheckLicense"
                        :label="title"></v-checkbox>
          </v-list-item>
        </v-list-group>
        <v-list-group value="Seatheight">
          <template v-slot:activator="{ props }">
            <v-list-item
                v-bind="props"
                prepend-icon="mdi-human-male-height"
                title="Min. Zithoogte(MM)"
            ></v-list-item>
          </template>
          <v-slider
              v-model="slider"
              class="align-center"
              :thumb-size="10"
              thumb-label="always"
              :max="seatHeight.max"
              :min="seatHeight.min"
              step="10"
          ></v-slider>
        </v-list-group>
        <v-list-group value="types">
          <template v-slot:activator="{ props }">
            <v-list-item
                v-bind="props"
                prepend-icon="mdi-motorbike"
                title="Motortypes"
            ></v-list-item>
          </template>
          <v-list-item height="50px" v-for="(title, i) in type" :key="i">
            <v-checkbox height="50px" v-model="additional_grouped" :value="title" @change="uniqueCheckType"
                        :label="title"></v-checkbox>
          </v-list-item>
        </v-list-group>
        <v-list-group value="brands">
          <template v-slot:activator="{ props }">
            <v-list-item
                v-bind="props"
                prepend-icon="mdi-decagram-outline"
                title="Merken"
            ></v-list-item>
          </template>
          <v-list-item height="50px" v-for="(title, i) in brand" :key="i">
            <v-checkbox height="50px" v-model="additional_grouped" :value="title" @change="uniqueCheckBrand"
                        :label="title"></v-checkbox>
          </v-list-item>
        </v-list-group>
      </v-list>
    </v-navigation-drawer>


    <v-app-bar elevation=2 color="white">
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
      <v-spacer></v-spacer>
      <v-btn icon to="/">
        <v-icon icon="mdi-home"></v-icon>
      </v-btn>
    </v-app-bar>
  </v-container>
</template>

<script>
import dataService from "@/services/DataService";

export default {
  name: "app-bar",
  emits: ['changeLicense', 'changeSeatheight'],
  data: () => ({
    drawer: null,
    additional_grouped: [],
    licenses: ['A1', 'A2', "A(All)"],
    brand: ['Yamaha', 'BMW', 'Honda'],
    type: ['Supersport', 'Sport', 'TourSport', 'Tour', 'Naked', 'SuperMotard', 'Muscle Bike', 'Adventure/Allroad'],
    seatHeight: {min: 0, max: 600},
    slider: 10,

  }),

  mounted() {
    dataService.getBrands().then((response) => {
      this.brand = response.data
    })
    dataService.getTypes().then((response) => {
      this.type = response.data
    })
  },

  watch: {
    slider: function (val) {
      if (val) {
        this.$emit("changeSeatheight", val);
      }
    }
  },
  methods: {
    uniqueCheckLicense(e) {
      this.additional_grouped = [];
      if (e.target.checked) {
        this.additional_grouped.push(e.target.value);
        this.$emit("changeLicense", e.target.value);
      }
      if (this.additional_grouped.length === 0) {
        this.$emit("changeLicense", "none");
      }
    },
    uniqueCheckBrand(e) {
      this.additional_grouped = [];
      if (e.target.checked) {
        this.additional_grouped.push(e.target.value);
        this.$emit("changeBrand", e.target.value);
      }
      if (this.additional_grouped.length === 0) {
        this.$emit("changeBrand", "none");
      }
    },
    uniqueCheckType(e) {
      this.additional_grouped = [];
      if (e.target.checked) {
        this.additional_grouped.push(e.target.value);
        this.$emit("changeType", e.target.value);
      }
      if (this.additional_grouped.length === 0) {
        this.$emit("changeType", "none");
      }
    },
  }
}
</script>
