<template>
  <v-dialog max-width="600" v-model="dialog">
    <v-card color="primary" class="mx-auto">
      <v-img v-if="!showError" to="/" width="500px" height="500px" src="envalopDaniel.png"></v-img>
      <v-card-title v-else>Error! Excuses we werken er aan om het op te lossen!</v-card-title>
      <v-card-text>{{errorMessage}}</v-card-text>
      <v-card-actions>
        <v-btn color="black" block @click="dialog = false">Sluiten</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
  <v-container>
    <app-bar/>
    <h1>Contactveld</h1>
    <p>Als team motorvergelijken zijn we continue bezig met verbeteren, heb je opmerkingen, vragen of wil je graag jouw
      motor op de website?<br>
      Laat een berichtje achter en we komen zo snel mogelijk bij je terug!</p>
    <v-form
        ma-3
        ref="form"
        v-model="valid"
        lazy-validation
    >
      <v-text-field
          v-model="name"
          :counter="10"
          :rules="nameRules"
          label="Name"
          required
      ></v-text-field>

      <v-text-field
          v-model="email"
          :rules="emailRules"
          label="E-mail"
          required
      ></v-text-field>
      <v-textarea
          v-model="message"
          label="Bericht"
          required
      ></v-textarea>

      <v-btn
          :disabled="!valid"
          color="success"
          class="mr-4"
          @click="send"
      >
        Verzenden
      </v-btn>
    </v-form>
  </v-container>
</template>

<script>
import AppBar from "@/components/app-bar-default.vue";
import dataService from "@/services/DataService";

export default {
  components: {AppBar},
  data: () => ({
    valid: true,
    name: '',
    nameRules: [
      v => !!v || 'Name is required',
      v => (v && v.length <= 10) || 'Name must be less than 10 characters',
    ],
    email: '',
    emailRules: [
      v => !!v || 'E-mail is required',
      v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
    ],
    message: '',
    dialog: false,
    showError: false
  }),
  methods: {
    send() {
      this.$refs.form.validate()
      dataService.sendMessage({
        'email': this.email,
        'name': this.name,
        'message': this.message
      }).catch(() => {
        this.dialog = true;
        this.showError = true;
      }).then(() => {
            this.dialog = true;
            this.showError = false;
          }
      )
    }
  }
}
</script>